import React, { useState, useEffect } from 'react';
import Nav from './components/Nav';
import Profil from './components/Profil';
import Projects from './components/Projects';
import SideBar from './components/SibeBar';
import { AnimatePresence } from 'framer-motion';
import Contact from './components/Contact';
import Footer from './components/Footer';
import NavSmart from './components/NavSmart';
import Carousel from './components/Carousel';

const App = () => {
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);
  const [showSideBar, setShowSideBar] = useState(false);

  const handleResize = () => {
    setScreenWidth(window.innerWidth);
  };

  const handleScroll = () => {
    const profilSection = document.querySelector('.profil');
    const scrollY = window.scrollY;

    if (profilSection) {
      const profilSectionRect = profilSection.getBoundingClientRect();
      setShowSideBar(scrollY >= profilSectionRect.bottom);
    }
  };
  

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const isMobile = screenWidth < 1350;

  return (
    <main>
      {isMobile ? <NavSmart/> : <Nav /> }
      <AnimatePresence>
        {showSideBar && screenWidth > 900 && <SideBar />}
      </AnimatePresence>
      <Profil /> 
      {isMobile ? <Projects /> : <Carousel/> }
      <Contact/>
      <Footer/>
    </main>
  );
};

export default App;
