import React from 'react';
import {  motion } from 'framer-motion';

const SibeBar = () => {
    return (
        <motion.div className='sidebar' initial={{opacity: 0, scale: 0.4}} animate={{opacity: 1, scale: 1}} transition={{ duration: .6 }} exit={{opacity:0, scale: 0.4, transition: {duration: .2}}}>
            <div className='background-image-container'></div>
            <ul>
                <li><a href='#top'>Profil</a></li>
                <li><a href='#projects-container'>Réalisations</a></li>
                <li><a href='#form-container'>Contact</a></li>
            </ul>
        </motion.div>
    );
};

export default SibeBar;