import React from 'react';
import { projectsData } from '../data/projectsData';
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import 'react-awesome-slider/dist/custom-animations/open-animation.css';
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'

const Carousel = () => {
    return (
        <div className='carousel-container' id='projects-container'>
            
            <svg className='project-line' viewBox="0 0 800 400"><path d="M11.958127975463867,214.34976196289062C15.994002342224121,208.07173665364584,30.49325466156006,191.62929153442383,36.17337417602539,176.68161010742188C41.85349369049072,161.73392868041992,37.51866594950358,136.0239117940267,46.03884506225586,124.6636734008789C54.55902417500814,113.30343500773112,64.42449378967285,111.50971603393555,87.29444885253906,108.52017974853516C110.16440391540527,105.53064346313477,154.409548441569,110.61285400390625,183.25857543945312,106.72645568847656C212.10760243733725,102.84005737304688,239.16290283203125,94.76830673217773,260.38861083984375,85.20178985595703C281.61431884765625,75.63527297973633,295.5156656901042,63.527652740478516,310.6128234863281,49.327354431152344C325.70998128255206,35.12705612182617,344.24510192871094,8.22122573852539,350.9715576171875,0" fill="none" strokeWidth="2" stroke="url(&quot;#SvgjsLinearGradient1003&quot;)" strokeLinecap="round" strokeDasharray="2 7"></path><defs><linearGradient id="SvgjsLinearGradient1003"><stop stopColor="hsl(162, 100%, 58%)" offset="0"></stop><stop stopColor="hsl(270, 73%, 53%)" offset="1"></stop></linearGradient></defs></svg>
            <svg className='project-line-2' viewBox="0 0 800 400"><path d="M81.01641845703125,397.3094177246094C107.47381846110027,396.4125569661458,200.1494598388672,395.66517639160156,239.7608184814453,391.9282531738281C279.37217712402344,388.1913299560547,289.68606821695965,381.46485900878906,318.6845703125,374.88787841796875C347.68307240804035,368.31089782714844,383.4080352783203,361.58445739746094,413.7518310546875,352.46636962890625C444.0956268310547,343.34828186035156,468.0119222005208,331.39011637369794,500.7473449707031,320.1793518066406C533.4827677408854,308.9685872395833,576.0836537679037,285.2017822265625,610.1643676757812,285.2017822265625C644.2450815836588,285.2017822265625,674.588877360026,318.23615010579425,705.2316284179688,320.1793518066406C735.8743794759115,322.122553507487,779.222666422526,300.7473856608073,794.0208740234375,296.8609924316406" fill="none" strokeWidth="1" stroke="url(&quot;#SvgjsLinearGradient1005&quot;)" strokeLinecap="round" strokeDasharray="4 7"></path><defs><linearGradient id="SvgjsLinearGradient1005"><stop stopColor="hsl(162, 100%, 58%)" offset="0"></stop><stop stopColor="hsl(270, 73%, 53%)" offset="1"></stop></linearGradient></defs></svg>

            <h3>Réalisations</h3>
            <AwesomeSlider animation="openAnimation" mobileTouch>
                {projectsData.map((project) => (
                    <div key={project.id} className='carousel-card'>
                        <h2>{project.title}</h2>
                        <Zoom>
                        <picture>
                            <source srcSet={project.img} media="(min-width: 1921px)" />
                            <img src={project.medium_img} alt={`projet ${project.title}`} />
                        </picture>
                        </Zoom>
                        <h5>{project.languages.join(' - ')}</h5>
                        <p>{project.infos}</p>
                        <a className='btn-link' href={project.link === '' ? project.git : project.link} target='_blank' rel="noreferrer">{project.link === '' ? 'Voir le GitHub' : 'Visiter le site'}</a>
                    </div>
                ))}
            </AwesomeSlider>
        </div>
    );
};

export default Carousel;