import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const Contact = () => {
  const form = useRef();
  const [ref, inView] = useInView();

  const sendEmail = (e) => {
    e.preventDefault();
    const formMessage = document.querySelector('.form-message');

    emailjs.sendForm('default_service', 'template_qyf9v4m', form.current, '4dA6RqHOJXGVigV_e')
      .then((result) => {
          console.log(result.text);
          form.current.reset();
          formMessage.innerHTML = '<p class="success">Message envoyé</p>';
          setTimeout(() => {
            formMessage.innerHTML = '';
          }, 2500);
      }, (error) => {
          console.log(error.text);
          formMessage.innerHTML = `<p class="error">Une erreur s'est produite, veuillez réessayer</p>`
      });
  };

  const back = {
    hidden: {
      opacity: 0,
      transform: 'translateY(1500px) rotate(-35deg)',
    },
    visible: {
      opacity: 1,
      transform: 'translate(0)',
    },
  }
  const variants1 = {
    hidden: {
      opacity: 0,
      transform: 'translateY(-1500px) translateX(2000px) rotate(85deg)',
    },
    visible: {
      opacity: 1,
      transform: 'translate(0)',
    },
  }
  const variants2 = {
    hidden: {
      opacity: 0,
      transform: 'translateY(-1500px) translateX(-2000px) rotate(-85deg)',
    },
    visible: {
      opacity: 1,
      transform: 'translate(0)',
    },
  }
  const about = {
    hidden: {
      opacity: 0,
      transform: 'translateY(1500px) rotate(-35deg)',
    },
    visible: {
      opacity: 1,
      transform: 'translate(0)',
    },
  }
  const sub = {
    hidden: {
      transform: 'rotate(2deg)',
    },
    visible: {
      transform: 'rotate(-4deg)',
    }
  }
  const bubbles = {
    hidden: {
      height: 0,
      with: 0,
      opacity:0,
      transform: 'translateY(120px)',
    },
    visible: {
      opacity:1,
      height: 15,
      with: 15,
      transform: 'translateY(-80px)',
    }
  }
  const bubbles2 = {
    hidden: {
      height: 0,
      with: 0,
      opacity:0,
      transform: 'translateY(130px)',
    },
    visible: {
      height: 12,
      with: 12,
      opacity:1,
      transform: 'translateY(-100px)',
    }
  }
  const bubbles3 = {
    hidden: {
      height: 0,
      with: 0,
      opacity:0,
      transform: 'translateY(120px)',
    },
    visible: {
      height: 10,
      with: 10,
      opacity:1,
      transform: 'translateY(-90px)',
    }
  }

  const rivetsTop = Array(33).fill().map((_, index) => (
    <div className="rivets" style={{ left: `${index * 10 + 10}px`, top: '5px' }} key={index}></div>
  ));
  const rivetsBottom = Array(29).fill().map((_, index) => (
    <div className="rivets" style={{ left: `${index * 10 + 30}px`, top: '70px' }} key={index}></div>
  ));
  const rivetsFront = Array(5).fill().map((_, index) => (
    <div className="rivets" style={{ top: `${index * 12 + 15}px`, left: '25px' }} key={index}></div>
  ));
  const rivetsBack = Array(5).fill().map((_, index) => (
    <div className="rivets" style={{ top: `${index * 12 + 15}px`, right: '30px' }} key={index}></div>
  ));
  const rivetsPeri = Array(5).fill().map((_, index) => (
    <div className="rivets" style={{ top: `${index * 8 + 5}px`, left: '24px' }} key={index}></div>
  ));

  return (
    <div id="form-container" ref={ref} >
        <svg width="100%" height="100%" id="svg" viewBox="0 0 1440 690" xmlns="http://www.w3.org/2000/svg" className="waves transition duration-300 ease-in-out delay-150"><defs><linearGradient id="gradient" x1="0%" y1="50%" x2="100%" y2="50%"><stop offset="5%" stopColor="#50a5de"></stop><stop offset="95%" stopColor="#8ED1FC"></stop></linearGradient></defs><path d="M 0,700 C 0,700 0,233 0,233 C 91.62307692307692,271.22564102564104 183.24615384615385,309.451282051282 261,287 C 338.75384615384615,264.548717948718 402.63846153846157,181.4205128205128 488,179 C 573.3615384615384,176.5794871794872 680.2,254.86666666666667 748,280 C 815.8,305.1333333333333 844.5615384615385,277.11282051282046 922,241 C 999.4384615384615,204.88717948717954 1125.553846153846,160.6820512820513 1220,158 C 1314.446153846154,155.3179487179487 1377.2230769230769,194.15897435897435 1440,233 C 1440,233 1440,700 1440,700 Z" stroke="none" strokeWidth="0" fill="url(#gradient)" fillOpacity="0.53" className="waves wavestransition-all duration-300 ease-in-out delay-150 path-0"></path><defs><linearGradient id="gradient" x1="0%" y1="50%" x2="100%" y2="50%"><stop offset="5%" stopColor="#50a5de"></stop><stop offset="95%" stopColor="#8ED1FC"></stop></linearGradient></defs><path d="M 0,700 C 0,700 0,466 0,466 C 91.15897435897438,434.53076923076924 182.31794871794875,403.0615384615385 251,385 C 319.68205128205125,366.9384615384615 365.8871794871794,362.2846153846154 448,393 C 530.1128205128206,423.7153846153846 648.1333333333334,489.79999999999995 734,511 C 819.8666666666666,532.2 873.579487179487,508.5153846153846 946,506 C 1018.420512820513,503.4846153846154 1109.5487179487181,522.1384615384616 1195,519 C 1280.4512820512819,515.8615384615384 1360.225641025641,490.9307692307692 1440,466 C 1440,466 1440,700 1440,700 Z" stroke="none" strokeWidth="0" fill="url(#gradient)" fillOpacity="1" className="transition-all duration-300 ease-in-out delay-150 path-1"></path></svg>
        <h3>Contact</h3>
        {inView && (
        <> 
          <div className='message-shadow'>
          <div className="form-message"></div>
          </div>

          <motion.form ref={form} onSubmit={sendEmail} variants={back} initial='hidden' animate='visible' transition={{ duration: 0.8 }}>
            <div className="input-container">
                <motion.label variants={variants1} initial='hidden' animate='visible' transition={{ duration: 0.2, delay: 2.6 }}>Nom</motion.label>
              <motion.input type="text" name="name" required autoComplete='off' variants={variants2} initial='hidden' animate='visible' transition={{ duration: 0.4, delay: 2.3 }}/>
            </div>
              <div className="input-container">
                <motion.label variants={variants1} initial='hidden' animate='visible' transition={{ duration: 0.6, delay: 1.9 }}>Email</motion.label>
                <motion.input type="email" name="email" required autoComplete='off' variants={variants2} initial='hidden' animate='visible' transition={{ duration: 0.7, delay: 1.5 }}/>                      
              </div>
              <div className="input-container">
                <motion.label variants={variants1} initial='hidden' animate='visible' transition={{ duration: 0.8, delay: 1.1 }}>Message</motion.label>
                <motion.textarea name="message" required autoComplete='off' variants={variants2} initial='hidden' animate='visible' transition={{ duration: 0.9, delay: 0.7 }}/>                      
              </div>
              <div className="input-container">
                <motion.input className='btn-submit' type="submit" value="Envoyer" variants={variants1} initial='hidden' animate='visible' transition={{ duration: 1, delay: 0.1 }}/>                      
              </div>
          </motion.form>
          <motion.div className="about" variants={about} initial='hidden' animate='visible' transition={{ duration: 1.8, delay: 2.3 }}>
            <motion.div className="submarine" variants={sub} initial='hidden' animate='visible' transition={{ duration: 3.5, repeat:Infinity, repeatType: "reverse", ease: "easeInOut"}}>
              <div className="periscope-container">
                <div className='periscope'></div>
                {rivetsPeri}
              </div>
              <div className="window-container">
                <div className='window'>
                  <div className='window-reflect'></div>
                </div>                    
              </div>
              <p>&#8657; Contactez moi ! &#8657;</p>
              <div className="propeller"></div>
              <div className="propeller-2"></div>
              <div className="propeller-rod"></div>
              {rivetsTop}
              {rivetsBottom}
              {rivetsFront}
              {rivetsBack}
            </motion.div>
            <div className='bubbles-container'>
              <motion.div className='bubbles' variants={bubbles} initial='hidden' animate='visible' transition={{ duration: 2.5, delay: 2.3, repeat:Infinity, repeatDelay: 2.2, ease: "easeInOut" }}>
                <div className='bubble-dot'></div>
              </motion.div>
              <motion.div className='bubbles2' variants={bubbles2} initial='hidden' animate='visible' transition={{ duration: 2.4, delay: 2.3, repeat:Infinity, repeatDelay: 2, ease: "easeInOut" }}>
                <div className='bubble-dot'></div>
              </motion.div>
              <motion.div className='bubbles3' variants={bubbles3} initial='hidden' animate='visible' transition={{ duration: 2.2, delay: 2.3, repeat:Infinity, repeatDelay: 1.4, ease: "easeInOut" }}>
                <div className='bubble-dot'></div>
              </motion.div>              
            </div>

          </motion.div>
        </>)}
    </div>
  );
};

export default Contact;