import React from 'react';
import { motion } from 'framer-motion';

const Footer = () => {
    return (
        <footer>
            <a href='#top' aria-label="Retour en haut de page"><img className='to-top' src="./img/to-top.webp" alt="retour en haut de page" /></a>
            <p className='footer-info'>Ludovic Sagnal - 2023</p>
            <ul className='footer-links'>
                <li><a href="https://github.com/LudovicSagnal" target='blank' rel="noreferrer" ><motion.img src="./img/github.webp" alt="Lien vers profil github" whileHover={{y:5}}/></a></li>
                <li><a href="https://www.linkedin.com/in/ludovic-sagnal-300a71257/" target='blank' rel="noreferrer" ><motion.img src="./img/linkedin.webp" alt="Lien vers profil linkedin" whileHover={{y:5}}/></a></li>
                <li><a href="https://www.canva.com/design/DAFO1OtpF-4/c9kajhokfn2241x7Hc7f4g/view" target='blank' rel="noreferrer" ><motion.img src="./img/cv.webp" alt="Lien vers CV" whileHover={{y:5}}/></a></li>
            </ul>
        </footer>
    );
};

export default Footer;