import React, { useState } from 'react';

const NavSmart = () => {
  const [isOpen, setIsOpen] = useState(false);

  const handleNav = () => {
    setIsOpen(!isOpen);
    if (!isOpen) {
      document.body.style.overflowY = 'hidden';
    } else {
      document.body.style.overflowY = 'auto';
    }
  };

  return (
    <div className='smart-nav' onClick={handleNav}>
      <img src="./img/up-burger.webp" alt="" className={isOpen ? 'rotate-down' : ''} />
      <img src="./img/mid-burger.webp" alt="" className={isOpen ? 'hide' : ''} />
      <img src="./img/bottom-burger.webp" alt="" className={isOpen ? 'rotate-up' : ''} />
      <ul className={isOpen ? 'open-nav open' : 'open-nav'}>
          <li><a href="#top">Profil</a></li>
          <li><a href="#projects-container">Réalisations</a></li>
          <li><a href="#form-container">Contact</a></li>
      </ul>
      <div className={isOpen ? 'overlay' : ''}></div>
    </div>
  );
};

export default NavSmart;
