import React, { useState } from 'react';
import { projectsData } from '../data/projectsData';
// import {  motion } from 'framer-motion';

const Projects = () => {
    const [activeIndex, setActiveIndex] = useState(null);

    const handleClick = (index) => {
        if (activeIndex === index) {
            setActiveIndex(null);
        } else {
            setActiveIndex(index);
        }
    };

    return (
        <div id='projects-container' className='projects-container'>
            <h3>Réalisations</h3>
            <div className='projects'>
                { projectsData.map((project, index) => (
                    <div className="cards-container" key={index}>
                        <div className={`project-card ${activeIndex === index ? 'no-opacity' : ''}`} onClick={() => handleClick(index)}>
                            <h4>{project.title}</h4>
                            <img src={project.small_img} alt={`projet ${project.title}`} />
                        </div>
                        <div className={`project-card-back ${activeIndex === index ? '' : 'no-opacity'}`} onClick={() => handleClick(index)}>
                            <h4>{project.title}</h4>
                            <h5>{project.languages.join(' - ')}</h5>
                            <p>{project.infos}</p>
                            <a className='btn-link' href={project.link === '' ? project.git : project.link} target='_blank' rel="noreferrer">{project.link === '' ? 'Voir le GitHub' : 'Visiter le site'}</a>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Projects;
